import { Component, OnInit, ViewChild } from '@angular/core';
import { first } from 'rxjs/operators';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';

import { Company, Employee } from '@model';
import { CompanyDeductionEditorService } from './company-deduction-editor.service';
import { CompanyDeductionRemoveComponent } from './company-deduction-remove.component';
import { CompanyService } from '@entity';
import { CompanyDeductionItem, buildCommandForEdit, isNewCompanyDeductionItem } from '@shared/deductions';
import { LoggerService } from '@core';

@Component({
  selector: 'sp-company-deductions-table',
  templateUrl: './company-deductions-table.component.html'
})
export class CompanyDeductionsTableComponent implements OnInit {
  company$: Observable<Company>;

  @ViewChild('removeConfirmation') removeConfirmationModal: CompanyDeductionRemoveComponent;

  constructor(
    private companyService: CompanyService,
    private editorService: CompanyDeductionEditorService,
    private log: LoggerService,
    private router: Router,
  ) {  }

  ngOnInit() {
    this.company$ = this.companyService.currentCompany$;
  }

  /** Hide the elements been created (new state)
   *  Show only created ones.
   */
  get createdDeductions(): CompanyDeductionItem[] {
    return this.editorService.companyDeductionItems
      .filter(d => !isNewCompanyDeductionItem(d) )
      .sort((a, b) =>  a.deduction.codeType.localeCompare(b.deduction.codeType));
  }

  getMenuLabel(di: CompanyDeductionItem): string {
    const suffix = di.isPreTax ? ' (Pre-Tax)' :  di.isPostTax ? ' (Post-Tax)' : '';
    return di.deduction.codeType + suffix;
  }

  /**
   * Navigate to edit deductions
   * Retirements and most deductions are single instances: you can only instanciate it once.
   * But some types of deductions like Miscellaneous with/out Goals can be instanciated
   * till 5 times. Code is the discriminator of the instances to allow navigate to edit the specific one.
   */
  edit(d: CompanyDeductionItem) {
    this.editorService.command = buildCommandForEdit(d);
    if (d.isRetirement()) {
      this.router.navigate(['/employees/company-deductions/retirement']);
    } else {
      const code = d.getCode();
      if (code) {
        this.router.navigate(['/employees/company-deductions/deduction/' + d.deduction.codeType + '/' + code]);
      } else {
        this.router.navigate(['/employees/company-deductions/deduction/' + d.deduction.codeType]);
      }
    }
  }
  delete(d: CompanyDeductionItem): void {
    this.editorService.getEmployeesEnrolledInDeduction(d)
      .pipe(first()).subscribe(affectedEmployees => {
        this.doDelete(d, affectedEmployees);
      });
  }
  private doDelete(d: CompanyDeductionItem, affectedEmployees: Employee[]): void {
    const needsConfirmation = affectedEmployees.length > 0;
    if (needsConfirmation) {
      this.removeConfirmationModal.show(d.name, affectedEmployees)
          .pipe(first())
          .subscribe(result => {
            if (result) {
              this.confirmDelete(d);
            }
          });
    } else {
      this.confirmDelete(d);
    }
  }
  private confirmDelete(d: CompanyDeductionItem): void {
    this.editorService.delete(d).subscribe(
      _ => {
        this.router.navigate(['/employees/company-deductions']);
      },
      // TODO: Remove error handling that looks like this
      // because this does nothing that wasn't done at lower level
      // THE ONLY REASON to handle error in component is to display something (or swallow error)
      // JESUS: Please hunt for other examples and fix them.
      // JESUS: Ask Pedro did he have a specific reason for swallowing error here.
      error => this.log.error(error)
    );
  }
}
