import { Component, EventEmitter, Input, Output } from '@angular/core';

import { DeductionCategory, DeductionTitle, DeductionTitleDictionary } from '@shared/deductions';
import { dictionaryToArray } from '@core';

@Component({
  selector: 'sp-deductions-catalog',
  templateUrl: './deductions-catalog.component.html',
})
export class DeductionsCatalogComponent {
  @Input()
  catalog: DeductionCategory[];

  @Output() deductionTypeSelected = new EventEmitter<DeductionTitle>();

  getListOfDeductionTitles(titles: DeductionTitleDictionary): DeductionTitle[] {
    return dictionaryToArray(titles)
            .filter(t => !t.isHidden)
            .sort((a, b) =>  a.order - b.order);
  }

  selectType(dt: DeductionTitle): void {
    this.deductionTypeSelected.emit(dt);
  }
}
