import { Injectable } from '@angular/core';
import { Observable, of, combineLatest, Subject } from 'rxjs';
import { first, map, tap } from 'rxjs/operators';

import { CompanyDeduction,
         Deduction,
         DeductionPlanType,
         deductionIsCustomType,
         Employee,
         EmployeeDeduction,
         WellKnownDeductionsCodeTypeId
} from '@model';
import { CompanyDeductionItem,
         DeductionCategory,
         DeductionCommand,
         getDeductionCategories,
         isNewCompanyDeductionItem,
         updateTreeState
       } from '@shared/deductions';
import { CompanyDeductionService, DeductionService, EmployeeDeductionService, EmployeeService
       } from '@entity';

@Injectable({ providedIn: 'root' })
export class CompanyDeductionEditorService {
  companyDeductionItems: CompanyDeductionItem[];
  deductions: Deduction[];
  deductionCategories: DeductionCategory[];

  currentInEdit: CompanyDeductionItem;
  private _command: DeductionCommand;

  private employeeDeductionsToDelete: EmployeeDeduction[] = null;

  constructor(
    private deductionService: DeductionService,
    private companyDeductionService: CompanyDeductionService,
    private employeeDeductionService: EmployeeDeductionService,
    private employeeService: EmployeeService
  ) {}


  get command(): DeductionCommand {
    return this._command;
  }
  set command(cmd: DeductionCommand) {
    this._command = cmd;
  }
  getDeductionWithTaxMode(isPreTax: boolean): Deduction {
    const deductions = this.command ? this.command.deductions : [];
    const found = deductions.find(d => {
      if (isPreTax) {
        return d.deductionPlanTaxOptionCode === DeductionPlanType.PRETAX;
      } else if (isPreTax === false) {
        return d.deductionPlanTaxOptionCode === DeductionPlanType.POSTTAX;
      } else {
        return d.deductionPlanTaxOptionCode === DeductionPlanType.NOEEDEDUCT;
      }
    });
    return found;
  }



  /**
   * Create CompanyDeductionItem from a Deduction and add to cached CompanyDeductionItems
   * @param deduction Deduction master metadata for this item.
   */
  create(deduction: Deduction, code: string): CompanyDeductionItem {
    const cd = this.companyDeductionService.createFromDeduction(deduction);
    const item = new CompanyDeductionItem(deduction, true, [cd]);

    if (code) {
      item.setCode(code);
      let namePrefix: string;
      let abbreviationPrefix: string;
      if (deduction.codeTypeId === 23) {    // Misc Pre
        namePrefix = item.name + ' Pre #';
        abbreviationPrefix = 'MIPRE';
      } else  if (deduction.codeTypeId === 24) {    // Cafe 2
        namePrefix = item.name + ' #';
        abbreviationPrefix = 'CAFE';
      } else  if (deduction.codeTypeId === 34) {    //  Misc Post
        namePrefix = item.name + ' Post #';
        abbreviationPrefix = 'MIPRO';
      } else if (deduction.codeTypeId === 56) {  // Custom with Goal
        namePrefix = 'Custom with Goal #';
        abbreviationPrefix = 'CUSTWG';
      } else if (deduction.codeTypeId === 57) {  // Custom No Goal
        namePrefix = 'Custom No Goal #';
        abbreviationPrefix = 'CUSTNG';
      } else if (deduction.codeTypeId === 50) {  // Child Support
        namePrefix = 'Child Support #';
        abbreviationPrefix = 'CHILD';
      } else if (deduction.codeTypeId === 51) {  // Garnishment with Goal
        namePrefix = 'Garnishment with Goal #';
        abbreviationPrefix = 'GARWG';
      } else if (deduction.codeTypeId === 59) {  // Garnishment No Goal
        namePrefix = 'Garnishment No Goal #';
        abbreviationPrefix = 'GARNG';
      }

      if (namePrefix && abbreviationPrefix) {
        const proposal = this.proposeNameAndAbbreviation(deduction.codeTypeId, namePrefix, abbreviationPrefix);
        item.name = proposal.name;
        item.abbreviation = proposal.abbreviation;
      }
    }

    this.companyDeductionItems.push(item);
    this.currentInEdit = item;
    this.updateCatalog();
    return item;
  }

  /**  a unique name and abbreviation for a count-down deduction instance.
   * Looks for the next available instance number
   */
  proposeNameAndAbbreviation(codeTypeId: number,
                             namePrefix: string,
                             abbreviationPrefix: string): { name: string, abbreviation: string } {
    const items = this.companyDeductionItems.filter(d => d.deduction.codeTypeId === codeTypeId);
    let index = 1;
    let notUnique = true;
    let name = '';
    let abbreviation = '';

    while (notUnique) {
      name = namePrefix + index.toString();
      abbreviation = abbreviationPrefix + index.toString();

      const foundName = items.find(d => d.name === name);
      const foundAbbreviation = items.find(d => d.abbreviation === abbreviation);

      index++;
      notUnique = !!foundName || !!foundAbbreviation;
    }
    return {
      name,
      abbreviation
    };
  }

  createByCodeTypeId(tid: number): CompanyDeduction {
    const found = this.deductions.find(t => t.codeTypeId === tid);
    if (!found) {
      throw new Error(`Deduction Type ${tid} not found.`);
    }
    const cd = this.companyDeductionService.createFromDeduction(found);
    return cd;
  }

  getDeductionCodeTypeIdByTaxMode(codeType: string, isPreTax?: boolean): number {
    const found = this.deductions.filter(t => t.codeType === codeType)
                                 .find(d => {
      if (isPreTax === true) {
        return d.deductionPlanTaxOptionCode === DeductionPlanType.PRETAX;
      } else if (isPreTax === false) {
        return d.deductionPlanTaxOptionCode === DeductionPlanType.POSTTAX;
      } else {
        return true; // first one
      }
    });
    if (!found) {
      throw new Error(`Deduction Type ${codeType} not found.`);
    }
    return found.codeTypeId;
  }

  /**
   * Generates a code number to recognize when more than one instance of a type is needed.
   * Examples: Miscellaneous deductions for:
   * Miscellaneous with Goal (up to 5) or
   * Miscellaneous NO Goal (up to 5)
   * @param codeTypeId Type of deduction
   */
  generateCode(codeTypeId: number): string {
    if (
      codeTypeId === WellKnownDeductionsCodeTypeId.MiscellaneousNoGoal ||
      codeTypeId === WellKnownDeductionsCodeTypeId.MiscellaneousWithGoal
    ) {
      const slots: any = { '1': true, '2': true, '3': true, '4': true, '5': true };
      this.companyDeductionItems.forEach(cdi => {
        const code = cdi.getCode();
        if (code && cdi.deduction.codeTypeId === codeTypeId) {
          slots[code] = false;
        }
      });
      const found = Object.keys(slots).find(it => slots[it] === true);
      return found ? found : '6';
    }
    return '1'; // no multiple instances (singleton)
  }

  delete(companyDeductionItem: CompanyDeductionItem): Observable<any> {
    const deleteSource = new Subject();
    // delete employee deductions affected
    this.employeeDeductionService.getDeductionsLinkedTo(
      companyDeductionItem.companyDeductions[0].id
    ).pipe(first()).subscribe((employeeDeductionsToDelete) => {

      this.employeeDeductionsToDelete = employeeDeductionsToDelete;
      companyDeductionItem.isDeleted = true;
      this.updateCatalog();
      deleteSource.next( this.save() );

    });
    return deleteSource.asObservable();
  }

  /**
   * Find or create the CompanyDeductionItem for the give Deduction
   * @param deduction Deduction master metadata for this item
   * @param code Number of instance of the deductions (f.e. used in Miscellaneous Wit/out Goals)
   * Not needed for single-instance deductions
   */
  edit(deduction: Deduction, code: string): CompanyDeductionItem {
    let cdi: CompanyDeductionItem;
    if (deductionIsCustomType(deduction) && !code) {
      cdi = this.create(deduction, '1');
    } else {
      const foundInstance = this.foundDeductionInstance(deduction, code);
      cdi = foundInstance || this.create(deduction, code);
    }
    return this.editCompanyDeductionItem(cdi);
  }
  editCompanyDeductionItem(cdi: CompanyDeductionItem): CompanyDeductionItem {
    this.currentInEdit = cdi;
    this.updateCatalog();
    return cdi;
  }

  endEdit(): void {
    this.currentInEdit = null;
    this.updateCatalog();
  }

  /**
   * Returns a deduction of type catchup if defined
   */
  getCatchupDeduction(): CompanyDeductionItem {
    return this.companyDeductionItems.find(d => d.isCatchUp);
  }
  /** Creates a corresponding catchup plan for a deduction plan
   * @param cdi: base deduction plan
  */
  createCatchUpFor(cdi: CompanyDeductionItem): CompanyDeductionItem {
    const catchUpTid = this.getCorrespondingCatchUpPlanFor(cdi.deduction.codeTypeId);
    if (!catchUpTid) {
      return null;
    }
    const catchUpDeduction = this.deductions.find(d => d.codeTypeId === catchUpTid);
    const cd = this.companyDeductionService.createFromDeduction(catchUpDeduction);
    const item = new CompanyDeductionItem(catchUpDeduction, true, [cd]);
    this.companyDeductionItems.push(item);
    this.updateCatalog();
    return item;
  }

  getEmployeesEnrolledInDeduction(d: CompanyDeductionItem): Observable<Employee[]> {
    const employees$ = this.employeeService.entities$;
    const eds$ = this.employeeDeductionService.entities$;
    return combineLatest(employees$, eds$).pipe(
      map(([employees, eds]) => {
        return employees.filter(e => {
          const found = eds.find(ed1 =>
            ed1.employeeId === e.id &&
            ed1.companyDeductionId === d.companyDeductions[0].id );
          return found;
        });
      }
    ));
  }

  private foundDeductionInstance(deduction: Deduction, code: string): CompanyDeductionItem {
    return this.companyDeductionItems.find(it => {
      if (code) {
        const instanceCode = it.getCode();
        return it.deduction.codeTypeId === deduction.codeTypeId && instanceCode === code;
      } else {
        return it.deduction.codeTypeId === deduction.codeTypeId;
      }
    });
  }

  /**
   * Return boolean observable when the
   * deductions master/metadata have been loaded
   * and service is listening for current company deductions
   * (which are cached in this service).
   * Always emits true when ready.
   */
  ready(): Observable<boolean> {
    if (this.deductions) {
      return of(true);
    }

    return this.deductionService.deductions$.pipe(
      first(),
      tap(deductions => {
        this.deductions = deductions;

        // No need to unsubscribe because this service never dies
        this.companyDeductionService.entities$.subscribe(cds => {
          const items = this.mapToItems(cds);
          this.companyDeductionItems = items;
          if (!this.deductionCategories) {
            // cache to preserve visual state expand/collapse after first load
            this.deductionCategories = getDeductionCategories(deductions);
          }
          this.updateCatalog();
        });
      }),
      map(() => true)
    );
  }

  /**
   * Return the terminating, multi-entity-save Observable of the new/changed/deleted CompanyDeductions.
   * Must subscribe to execute save; no need to unsubscribe.
   * Cleans cached CompanyDeductionItems immediately, whether or not subscribed.
   */
  save(): Observable<any> {
    if (this.employeeDeductionsToDelete) {
      this.employeeDeductionsToDelete.forEach(ed => {
        ed.isRemoved = true;
      });
    }
    this.employeeDeductionService.save(this.employeeDeductionsToDelete)
                                 .subscribe();
    this.employeeDeductionsToDelete = null;

    // save the dirty items
    const toSave = this.companyDeductionItems.filter(d => d.isDirty || d.isDeleted);
    // mark CompanyDeduction rows for deletion
    toSave
      .filter(cdi => cdi.isDeleted)
      .forEach(cdi => {
        cdi.companyDeductions.forEach(cd => {
          cd.isRemoved = true;
        });
      });
    // propagate changes
    toSave.filter(cdi => cdi.isDirty && !cdi.isDeleted)
          .forEach(cdi => cdi.viewModelToEntity());

    // flatten each item's CompanyDeductions into a single array-to-save
    const changes = toSave.map(d => d.companyDeductions)
                          .reduce((a, b) => a.concat(b), []);

    const ret = this.companyDeductionService.save(changes);

    // Todo: Why is the following down here? Move above the save (below toSave)? Would be easier to read.
    // PJ: save is checking for isRemoved to do real deletion.
    // PJ: Need to clean in-mem state after saving.

    // items are no longer dirty after save
    this.companyDeductionItems.forEach(d => (d.isDirty = false));
    this.purgeDeleted();

    return ret;
  }

  // #region private helpers

  /**
   * Map CompanyDeductions into CompanyDeductionItems
   */
  private mapToItems(companyDeductions: CompanyDeduction[]): CompanyDeductionItem[] {
    // Current Assumption: 1 Company Deduction -> 1 Item Deduction
    return companyDeductions.map(d => {
      const deductionType = this.deductions.find(md => md.codeTypeId === d.deductionCodeTypeId);
      const item = new CompanyDeductionItem(deductionType, false, [d]);
      return item;
    });
  }

  /**
   * Remove the deleted CompanyDeductionItems and
   * remove the "isRemoved" CompanyDeductions from the remaining, active items
   */
  private purgeDeleted(): void {
    this.companyDeductionItems = this.companyDeductionItems
      .filter(item => item.isDeleted !== true)
      .map(item => {
        // exclude item's removed company deductions
        item.companyDeductions = item.companyDeductions.filter(d => d.isRemoved !== true);
        return item;
      });
  }

  private updateCatalog(): void {
    updateTreeState(
      this.deductionCategories,
      this.companyDeductionItems.filter(cdi => !isNewCompanyDeductionItem(cdi)),
      this.currentInEdit
    );
  }

  /** Returns correspondence between a retirement plan and a catchup plan */
  private getCorrespondingCatchUpPlanFor(tid: number): number {
      const found = this.deductions.find(d => d.retireAddon === tid);
      return found ? found.codeTypeId : null;
  }

  // #endregion private helpers
}


