import { Component, ElementRef, ViewChild } from '@angular/core';
import { NgbModal, NgbModalRef } from '@ng-bootstrap/ng-bootstrap';
import { from, Observable, Subject } from 'rxjs';

import { Employee } from '@app/model';

@Component({
  selector: 'sp-company-deduction-remove',
  templateUrl: 'company-deduction-remove.component.html'
})
export class CompanyDeductionRemoveComponent {
  @ViewChild('content') modalContent: ElementRef;

  deleteConfirmation: '';

  get isDeleteConfirmed(): boolean {
    return /delete/i.test(this.deleteConfirmation);
  }
  obs = new Observable<boolean>();
  source: Subject<boolean>;
  private modalRef: NgbModalRef;

  deductionName: string;
  employeeList: Employee[] = [];

  constructor(private modalService: NgbModal) {
  }

  show(deductionName: string, employeeList: Employee[]): Observable<boolean> {
    this.deductionName = deductionName;
    this.employeeList = employeeList;
    this.source = new Subject<boolean>();
    this.obs = from(this.source);
    this.modalRef = this.modalService.open(this.modalContent);
    this.modalRef.result.then(res => {
      this.close(res);
      this.modalRef = null;
    });
    return this.obs;
  }
  close(reason: string): void {
    if (reason === 'delete') {
      this.modalRef.close();
      this.source.next(true);
    } else {
      this.modalRef.dismiss();
      this.source.next(false);
    }
  }
}
