import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { CompanyDeductionEditorService } from './company-deduction-editor.service';
import { buildCommandForCreation,
         DeductionCategory,
         DeductionTitle } from '@shared/deductions';
import { WellKnownRoutes } from '@core';

class CompanyDeductionViewModel {
  catalog: DeductionCategory[] = [];
}

const companyDeductionsUrl = WellKnownRoutes.Employees + '/company-deductions';

@Component({
  selector: 'sp-company-deductions',
  templateUrl: './company-deductions-container.component.html'
})
export class CompanyDeductionsContainerComponent implements OnInit {
  vm: CompanyDeductionViewModel = new CompanyDeductionViewModel();
  selectedKey: string;
  showTable = false;

  constructor(
    private editorService: CompanyDeductionEditorService,
    private router: Router) {
  }

  ngOnInit() {
    this.vm = {
      catalog: this.editorService.deductionCategories
    };
  }

  onCatalogSelection(dt: DeductionTitle): void {
    this.selectedKey = dt.key;
    this.editorService.command = buildCommandForCreation(dt);
    const code = this.editorService.command.code;
    if (dt.key === 'Retirements') {
      this.router.navigate([companyDeductionsUrl + '/retirement']);
    } else {
      this.router.navigate([`${companyDeductionsUrl}/deduction/${dt.key}/${code}`]);
    }
  }
}
