import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { CompanyDeductionEditorComponent } from './company-deduction-editor.component';
import { CompanyDeductionRemoveComponent } from './company-deduction-remove.component';
import { CompanyDeductionsContainerComponent } from './company-deductions-container.component';
import { CompanyDeductionsGuard } from './company-deductions.guard';
import { CompanyDeductionsTableComponent } from './company-deductions-table.component';
import { CompanyRetirementComponent } from './company-retirement.component';
import { DeductionsCatalogComponent } from './deductions-catalog.component';
import { PageLeaveGuard } from '@core/routing-guards';
import { SharedModule } from '@shared/shared.module';

export const routes: Routes = [
  {
    // Expected full path: ~/employees/company-deductions
    path: '',
    component: CompanyDeductionsContainerComponent,
    canActivateChild: [CompanyDeductionsGuard],
    canDeactivate: [PageLeaveGuard],
    data: { title: 'Company Deductions' },
    children: [
      {
        path: '',
        component: CompanyDeductionsTableComponent,
        canDeactivate: [PageLeaveGuard],
        data: {
          title: 'Company Deductions',
          root: true   // root marks the root page to provide info to hide/show deductions-table
        }
      },
      {
        path: 'retirement',
        component: CompanyRetirementComponent,
        canDeactivate: [PageLeaveGuard],
        data: { title: 'Company Retirement Plan' }
      },
      {
        path: 'deduction/:id/:code', // sample: deduction/Miscellaneous+No+Goal/2
        component: CompanyDeductionEditorComponent,
        canDeactivate: [PageLeaveGuard],
        data: {}
      },
      {
        path: 'deduction/:id', // sample: deduction/Medical
        component: CompanyDeductionEditorComponent,
        canDeactivate: [PageLeaveGuard],
        data: {}
      }
    ]
  }
];

@NgModule({
  imports: [SharedModule, RouterModule.forChild(routes)],
  declarations: [
    CompanyDeductionEditorComponent,
    CompanyDeductionsContainerComponent,
    CompanyDeductionsTableComponent,
    CompanyDeductionRemoveComponent,
    CompanyRetirementComponent,
    DeductionsCatalogComponent
  ]
})
export class CompanyDeductionsModule {}
