import { Component, OnInit, ElementRef } from '@angular/core';
import { SafeHtml } from '@angular/platform-browser';
import { Router } from '@angular/router';

import { Observable, of } from 'rxjs';
import { map } from 'rxjs/operators';

import { CompanyDeductionEditorService } from './company-deduction-editor.service';
import { CompanyDeductionItem } from '@shared/deductions';
import { CompanyDeduction, Deduction, MatchTypes,
         RetirementPlans, RetirementPlanTypes,
         RetirementPlanApply, EmployeeContributionModel
       } from '@model';
import { Logger } from 'ngrx-data';
import { WellKnownRoutes } from '@core';

class Retirement {
  retirementTypeName: string;
  plan: RetirementPlans;
  planType: RetirementPlanTypes;
  htmlDescription: SafeHtml | null;
  name: string;
  abbreviation: string;
  canChangeName: boolean;
  canChangeAbbreviation: boolean;
}

@Component({
  selector: 'sp-company-retirement',
  templateUrl: './company-retirement.component.html'
})
export class CompanyRetirementComponent implements OnInit {
  RetirementPlans = RetirementPlans;
  RetirementPlanTypes = RetirementPlanTypes;
  RetirementPlanApply = RetirementPlanApply;
  MatchTypes = MatchTypes;
  EmployeeContributionModel = EmployeeContributionModel;

  vm: Retirement;
  deductions: Deduction[];
  currentDeductionItem: CompanyDeductionItem;

  isNew = false;
  private cancelSave = false;

  constructor(
      private editorService: CompanyDeductionEditorService,
      private hostEl: ElementRef,
      private logger: Logger,
      private router: Router,
    ) {}

  ngOnInit() {
    this.deductions = this.editorService.deductions;
    this.vm = new Retirement();
    this.loadVm();

    setTimeout(() => this.hostEl.nativeElement.scrollIntoView()); // must delay one tick
  }

  done(): void {
    this.editorService.endEdit();
    this.router.navigate([ WellKnownRoutes.CompanyDeductions ]);
  }
  cancel(): void {
    this.editorService.endEdit();
    this.cancelSave = true;
    this.router.navigate([ WellKnownRoutes.CompanyDeductions ]);
  }
  delete() {
    this.editorService.endEdit();
    this.editorService.delete(this.currentDeductionItem).subscribe(
      _ => {},
      error => this.logger.error(error)
    );
    this.router.navigate([ WellKnownRoutes.CompanyDeductions ]);
  }

  canLeaveAfterSave(): Observable<any> {
    if (this.cancelSave) {
      this.cancelSave = false;
      return of(true);
    }
    return this.save().pipe(map(d => {
      this.editorService.endEdit();
      return true;
    }));
  }
  get showAdditionalPlans(): boolean {
    const vm = this.vm;
    return vm.plan &&
           (vm.planType || vm.plan === RetirementPlans.Plan403B ) && // Has plan and plan type except 403b
           !(vm.plan === RetirementPlans.IRA &&
             vm.planType === RetirementPlanTypes.SepIRA);  // but not for SEP IRA
  }

  private save(): Observable<any> {
    this.vmToEntity();
    return this.editorService.save();
  }
  private vmToEntity(): void {
    if (!this.currentDeductionItem) {
      return;
    }
    this.currentDeductionItem.name = this.vm.name.trim();
    this.currentDeductionItem.abbreviation = this.vm.abbreviation.trim();

    const prev = this.currentDeductionItem.companyDeductions[0];
    let target: CompanyDeduction;
    let changedType = false;
    const tid = buildRetirementTypeId(this.vm);
    if (tid !== prev.deductionCodeTypeId) {
      // change type: remove previous and create new one
      prev.isRemoved = true;
      target = this.editorService.createByCodeTypeId(tid);
      this.currentDeductionItem.companyDeductions.push(target);
      changedType = true;
    } else {
      target = prev;
    }
    this.saveRetirementsProperties(target);
  }

  private saveRetirementsProperties(cd: CompanyDeduction): void {
    cd.description  = this.vm.name;
    cd.abbreviation = this.vm.abbreviation;
  }

  private loadVm(): void {
    const retirementDeduction = this.editorService.companyDeductionItems.find(
      d => d.deduction.codeGroupId === 1);

    let cd: CompanyDeductionItem;
    this.isNew = !retirementDeduction;
    if (retirementDeduction) {
      // edit an existing one
      cd = this.editorService.editCompanyDeductionItem(retirementDeduction);
    } else {
      // create new retirement deduction
      const newRetirementType = this.deductions.find(d => d.codeType === '401K');
      cd = this.editorService.create(newRetirementType, '1');
    }

    const baseDed = cd.companyDeductions[0];
    this.currentDeductionItem = cd;
    this.currentDeductionItem.isDirty = true;

    this.vm = {
      retirementTypeName: this.vm.retirementTypeName,
      plan: getPlanFrom(baseDed.deductionCodeTypeId),
      planType: getPlanTypeFrom(baseDed.deductionCodeTypeId),
      htmlDescription: cd.deduction.definition,
      name: cd.name || cd.deduction.defaultDescription || '',
      abbreviation: cd.abbreviation || cd.deduction.defaultAbbreviation || '',
      canChangeName: cd.deduction.canChangeDescription,
      canChangeAbbreviation: cd.deduction.canChangeAbbreviation,
    };
  }

  configureEditorForPlan(): void {
    this.vm.planType = getDefaultPlanTypeForPlan(this.vm.plan);
    this.configureEditor();
  }

  configureEditor(): void {
    const deduction = this.getDeduction();
    this.setDescriptionPlan(deduction);
  }

  setDescriptionPlan(deduction: Deduction) {
    if (deduction) {
      this.vm.htmlDescription = deduction.definition;
      this.vm.canChangeName = deduction.canChangeDescription;
      this.vm.canChangeAbbreviation = deduction.canChangeAbbreviation;

      this.vm.name = (deduction.defaultDescription || 'Deduction 1').trim();
      this.vm.abbreviation = (deduction.defaultAbbreviation || 'RET').trim();
    } else {
      this.vm.htmlDescription = null;
      this.vm.canChangeName = false;
      this.vm.canChangeAbbreviation = false;
      this.vm.name = null;
      this.vm.abbreviation = null;
    }
  }

  getDeduction(): Deduction {
    if (!this.deductions || this.vm.plan === null) {
      return null;
    }
    if (this.vm.plan === RetirementPlans.Plan403B) {
      const code403 = build403BTypeId(this.vm);
      return this.deductions.find(d => d.codeTypeId === code403);
    }
    if (this.vm.plan === RetirementPlans.IRA) {
      const codeIRA = buildIRATypeId(this.vm);
      return this.deductions.find(d => d.codeTypeId === codeIRA);
    }
    if (this.vm.plan === RetirementPlans.Plan401K) {
      const code401 = build401KTypeId(this.vm);
      return this.deductions.find(d => d.codeTypeId === code401);
    }
    return null;
  }
}

function getPlanFrom(codeTypeId: number): RetirementPlans {
  switch (codeTypeId) {
    case 7:
    case 8:
    case 9:
    case 10:
    case 11:
    case 14:
    case 15:
    case 16:
    case 60:
    case 65:
    case 71: return RetirementPlans.Plan401K;

    case 63:
    case 13: // 'SMPIRA'
    case 17:  return RetirementPlans.IRA;

    case 12:  return RetirementPlans.Plan403B;
    default:
      throw new Error('codeTypeId not supported:' + codeTypeId);
  }
}

function getPlanTypeFrom(codeTypeId: number): RetirementPlanTypes {
  switch (codeTypeId) {
    case 60:
    case 10: return RetirementPlanTypes.P401KTraditional;
    case 65:
    case 15: return RetirementPlanTypes.P401KSafeHarbor;
    case 71:
    case 19: return RetirementPlanTypes.P401KSimple;
    case 63:
    case 13: return RetirementPlanTypes.SimpleIRA;
    case 17: return RetirementPlanTypes.SepIRA;
    case 12:  // 403B
    case 62:
    default: return null;
  }
}

function buildRetirementTypeId(vm: Retirement): number {
  switch (vm.plan) {
    case RetirementPlans.IRA: return buildIRATypeId(vm);
    case RetirementPlans.Plan403B: return build403BTypeId(vm);
    case RetirementPlans.Plan401K: return build401KTypeId(vm);
    default: return null;
  }
}
function buildIRATypeId(vm: Retirement): number {
  /*
    13    Simple IRA
    17    SEP IRA
    63    Simple IRA Catch Up
   */
  switch (vm.planType) {
    case RetirementPlanTypes.SimpleIRA: return 13; // Simple IRA  (overlap with 401K ??? )
    case RetirementPlanTypes.SepIRA: return 17;  // SEP IRA
    default: return null;
  }
}
function build403BTypeId(vm: Retirement): number {
  /*
    62  403B Catch Up
    12  403B  (shares name with 401K) ???
    61  401K Tiered Catch Up  (shared with 403B ???? )
   */
  return 12; //  12  403B  (shares name with 401K) ???
}
function build401KTypeId(vm: Retirement): number {
  /*
     7  Safe Harbor Roth 401k Match
     8  Safe Harbor Roth 401k NE
     9  Roth 401K Tiered
    10  401K
    11  401K Tiered
  x 12  401K (shared with 403B ???? )
    14  Roth 401K
    15  Safe Harbor 401K
    16  Simple 401K
    18  Safe Harbor 401K (duplicate??)
    19  Simple 401K

    60  401K Catch Up
    x 61  401K Tiered Catch Up  (shared with 403B ???? )
    64  Roth 401K Catch Up
    65  Safe H 401K Catch Up
    66  Simple 401K Catch Up
    67  Roth 401K Tiered Catch Up
    68  Safe H Roth 401K NE Catchup
    69  Safe H 401K Match Catch Up
    70  Safe H Roth 401K Match Catchup
    71  Simple 401K Catch Up
  */

  switch (vm.planType) {
    case RetirementPlanTypes.P401KSimple: return 19;
    case RetirementPlanTypes.P401KSafeHarbor: return 15;
    case RetirementPlanTypes.P401KTraditional: return 10;
    default: return null;
  }
}

function getDefaultPlanTypeForPlan(plan: RetirementPlans): RetirementPlanTypes {
  switch (plan) {
    case RetirementPlans.IRA: return RetirementPlanTypes.SimpleIRA;
    case RetirementPlans.Plan403B: return null; // no planType
    case RetirementPlans.Plan401K: return RetirementPlanTypes.P401KTraditional;
    default:
      return null;
  }
}
