import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit, ElementRef } from '@angular/core';
import { map } from 'rxjs/operators';
import { Observable, of } from 'rxjs';
import { SafeHtml } from '@angular/platform-browser';

import { CompanyDeductionEditorService } from './company-deduction-editor.service';
import { CompanyDeductionItem,
         CreateCompanyDeductionCommand,
         DeductionCommand,
         EditCompanyDeductionCommand,
       } from '@shared/deductions';
import { Deduction } from '@model';
import { LoggerService, WellKnownRoutes } from '@core';

/** Private VM class with default values */
class DeductionViewModel {
  abbreviation = 'ABBR';
  canChangeAbbreviation = false;
  canChangeName = false;
  hasHSAContributionQuestion = true;
  hasPreOrPostTaxQuestion = true;
  hsaContribution: HsaMatchType;
  hsaMatchAllValue: number;
  hsaMatchMaxValue: number;
  hsaMatchPercent: number;
  htmlDescription: SafeHtml;
  isHsaMatchAllMaxAmount = true;
  isHsaMatchMaxAmount = true;
  name = 'Base name';
  planTaxOptionCode = '';
  isPreTax = false;
  isPostTax = false;

  constructor(
    public command?: DeductionCommand,
    /** key is the Deduction.codeType */
    public id?: string,
    public code?: string
  ) {
    if (!command) {
      return;
    }
    if (command.isNew) {
      this.configureForCreate(command as CreateCompanyDeductionCommand);
    } else {
      this.configureForEdit(command as EditCompanyDeductionCommand);
    }
  }
  configureForCreate(cmd: CreateCompanyDeductionCommand): void {
    this.hasPreOrPostTaxQuestion = cmd.askPreOrPostTax &&
          !cmd.forcePostTax && !cmd.forcePreTax;
    this.isPreTax = cmd.forcePreTax;
    this.isPostTax = cmd.forcePostTax;
  }
  configureForEdit(cmd: EditCompanyDeductionCommand): void {
    this.hasPreOrPostTaxQuestion = false;
    this.isPreTax = false;
    this.isPostTax = false;
  }
}

enum HsaMatchType {
  NoMatch = 'NOMATCH',
  Match = 'MATCH',
  MatchAll = 'MATCHALL'
}

// TODO: Use enums instead of magic values
@Component({
  selector: 'sp-company-deduction-editor',
  templateUrl: './company-deduction-editor.component.html'
})
export class CompanyDeductionEditorComponent implements OnInit {
  private currentDeductionItem: CompanyDeductionItem;
  HsaMatchType = HsaMatchType; // expose enum to template
  vm: DeductionViewModel = new DeductionViewModel();

  isNew = false;
  private cancelSave = false;

  constructor(
    private editorService: CompanyDeductionEditorService,
    private logger: LoggerService,
    private hostEl: ElementRef,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.vm = new DeductionViewModel();
  }

  ngOnInit() {
    const codeType = this.route.snapshot.params['id'];
    const code = this.route.snapshot.params['code'];
    this.createVm(codeType, code);
    setTimeout(() => this.hostEl.nativeElement.scrollIntoView()); // must delay one tick
  }

  canLeaveAfterSave(): Observable<any> {
    if (this.cancelSave) {
      this.cancelSave = false;
      return of(true);
    }
    return this.save().pipe(map(d => {
      this.editorService.endEdit();
      return true;
    }));
  }

  get canConfirm(): boolean {
    return !(this.vm.hasPreOrPostTaxQuestion &&
             this.vm.planTaxOptionCode === ''
            );
  }

  private createVm(codeType: string, code: string) {
    const cmd = this.editorService.command;
    if (!cmd) {
      this.router.navigate([ WellKnownRoutes.CompanyDeductions ]);
      return;
    }
    this.vm = new DeductionViewModel(cmd, codeType, code);
    const deductions = this.editorService.deductions;

    const isHsa = codeType === 'HSA';
    const deduction = isHsa
      ? deductions.find(d => d.codeTypeId === 43) // first deduction on HSA (1 of 4)
      : deductions.find(d => d.codeType === codeType); // first matching code type will do

    if (!deduction) {
      // No deduction found!?
      // 1. A user added a bad id in the query string == deep linking mischief
      // 2. Bad grouping category with no deductions == programmer error
      this.logger.error(`No deduction for codeType: "${codeType}"`, deduction);
      this.router.navigate([ WellKnownRoutes.CompanyDeductions ]);
      return;
    }

    this.isNew = cmd.isNew;
    if (cmd.isNew) {
      this.currentDeductionItem = this.editorService.create(deduction, code);
    } else {
      this.currentDeductionItem = this.editorService.edit(deduction, code);
    }


    this.currentDeductionItem.isDirty = true;

    // fill the VM from deduction and item data
    this.vm.abbreviation = this.currentDeductionItem.abbreviation;
    this.vm.canChangeName = deduction.canChangeDescription;
    this.vm.canChangeAbbreviation = deduction.canChangeAbbreviation;
    this.vm.hasHSAContributionQuestion = isHsa ? false : this.hasHSAContributionQuestion(deduction);
    if (isHsa) {
      this.vm.hasPreOrPostTaxQuestion = false;
    }
    this.vm.name = this.currentDeductionItem.name;

    // Security: `definition` is always authored by the SP team and is considered to be safe.
    this.vm.htmlDescription = deduction.definition;

    if (this.vm.isPreTax) {
      this.updateDescription('PRETAX');
    }
    if (this.vm.isPostTax) {
      this.updateDescription('POSTTAX');
    }
  }

  updateDescription(taxMode: string): void {
    const isPreTax = taxMode === 'PRETAX' ? true
                                          : taxMode === 'POSTTAX' ? false
                                                                  : null;
    const deduction = this.editorService.getDeductionWithTaxMode(isPreTax);

    if (isPreTax) {
      this.vm.isPreTax = true;
      this.vm.isPostTax = false;
      this.vm.name = this.currentDeductionItem.deduction.codeType + ' Pre-Tax';
    } else if (isPreTax === false) {
      this.vm.isPreTax = false;
      this.vm.isPostTax = true;
      this.vm.name = this.currentDeductionItem.deduction.codeType + ' Post-Tax';
    } else {
      this.vm.isPreTax = false;
      this.vm.isPostTax = false;
    }

    if (deduction) {
      this.vm.htmlDescription = deduction.definition;
    } else {
      this.vm.htmlDescription = '';
    }
  }

  done(): void {
    this.editorService.endEdit();
    this.router.navigate([ WellKnownRoutes.CompanyDeductions ]);
  }
  cancel(): void {
    if (this.isNew) {
      this.delete();
      return;
    }
    this.editorService.endEdit();
    this.cancelSave = true;
    this.router.navigate([ WellKnownRoutes.CompanyDeductions ]);
  }
  delete() {
    this.editorService.endEdit();
    this.editorService.delete(this.currentDeductionItem).subscribe(
      _ => {},
      error => this.logger.error(error)
    );
    this.router.navigate([ WellKnownRoutes.CompanyDeductions ]);
  }

  save(): Observable<any> {
    const vm = this.vm;
    if (!vm) {
      return of(true);
    }
    const cdi = this.currentDeductionItem;
    if (cdi) {
      cdi.name = vm.name.trim();
      cdi.abbreviation = vm.abbreviation.trim();
      const hasChangedPrePost = canBePreOrPostTax(cdi.deduction.codeTypeId) &&
                                (cdi.isPreTax !== vm.isPreTax ||
                                 cdi.isPostTax !== vm.isPostTax);

      if (hasChangedPrePost) {
        cdi.companyDeductions[0].isRemoved = true;
        if (vm.isPreTax) {
          const preCTid = this.editorService.getDeductionCodeTypeIdByTaxMode(cdi.deduction.codeType, true);
          const preDed = this.editorService.createByCodeTypeId(preCTid);
          cdi.companyDeductions.push(preDed);
        } else if (vm.isPostTax) {
          const postCTid = this.editorService.getDeductionCodeTypeIdByTaxMode(cdi.deduction.codeType, false);
          const postDed = this.editorService.createByCodeTypeId(postCTid);
          cdi.companyDeductions.push(postDed);
        }
      }
      const cd = cdi.companyDeductions[0];
      cd.abbreviation = vm.abbreviation.trim();
      cd.description = vm.name.trim();
    }
    return this.editorService.save();
  }

  private hasHSAContributionQuestion(deduction: Deduction): boolean {
    return deduction.codeGroupId === 7; // HSA
  }
}


function canBePreOrPostTax(codeTypeId: number): boolean {
  switch (codeTypeId) {
    case 20:  // Medical pre
    case 21:  // Vision pre
    case 22:  // Dental pre
    case 30:  // Medial post
    case 31:  // Vision post
    case 32:  // Dental post
      return true;
    default:
      return false;
  }
}
