import { Injectable } from '@angular/core';

import { combineLatest, Observable, of } from 'rxjs';
import { concatMap, first, map } from 'rxjs/operators';

import { Address, Employee, EmployeePaymentMethod } from '@model';
import { changeItemFactory as cif } from '@utils';
import { CompanyEntityHelperService } from './company-entity-helper.service';
import { CurrentCompanyEntityCollectionService } from './current-company-entity-collection-service';
import { environment } from '@environment';
import { EntitySelectors } from '../entity-selectors';
import { TaxCollectorService } from '@core/services/tax-services/tax-collector.service';

@Injectable({providedIn: 'root'})
export class EmployeeService extends CurrentCompanyEntityCollectionService<Employee> {
  constructor(
    private entitySelectors: EntitySelectors,
    protected helper: CompanyEntityHelperService,
    private taxCollectorService: TaxCollectorService
  ) {
    super('Employee', helper);
  }


  saveNewEmployee(ee: Employee, home: Address, epay: EmployeePaymentMethod): Observable<Employee> {
    const changes = [
      cif.add('Address', home),
      cif.add('Employee', ee),
      cif.add('EmployeePaymentMethod', epay)
    ];
    return this.saveEntities({ changes }, environment.apiRoot + '/save/employee').pipe(
      map(_ => ee)
    );
  }

  saveEmployeeUpdate(ee: Employee, home: Address): Observable<Employee> {
    const changes = [
      cif.update('Address', home),
      cif.update('Employee', ee),
    ];
    return this.saveEntities({ changes }, environment.apiRoot + '/save/employee').pipe(
      map(_ => ee)
    );
  }
  /**
   * If Employee.isRemoved flag is changed, toggle and save isRemoved for the entire EE graph.
   * @param eeId Id of the Employee in question
   * @param isRemoved the proposed change to the "soft delete" flag
   */
  saveEmployeeIsRemovedById(eeId: string, isRemoved: boolean ): Observable<Employee> {
    return this.entityMap$.pipe(
      first(),
      map(eeMap => eeMap[eeId]),
      concatMap(ee => this.saveEmployeeIsRemoved(ee, isRemoved))
    );
  }

  // TODO: There might be much more work to toggling the isRemoved state.
  /**
   * If Employee.isRemoved flag is changed, toggle and save isRemoved for the entire EE graph.
   * @param ee Employee in question
   * @param isRemoved the proposed change to the "soft delete" flag
   */
  saveEmployeeIsRemoved(ee: Employee, isRemoved: boolean ): Observable<Employee> {
    if (ee == null || ee.isRemoved === isRemoved) {
      return of(null as Employee);
    }

    ee.isRemoved = isRemoved;

    const home$ = this.entitySelectors.address.entities$.pipe(
      map(addrs => addrs.find(a => a.id === ee.homeAddressId))
    );

    const payment$ = this.entitySelectors.employeePaymentMethod.entities$.pipe(
      map(pms => pms.find(pm => pm.employeeId === ee.id))
    );

    // TODO: Always use this multi-entity save
    const result$ = combineLatest(home$, payment$).pipe(
      first(),
      concatMap(([home, payment]) => {
        home.isRemoved = ee.isRemoved;
        const changes = [
          cif.update('Employee', ee),
          cif.update('Address', home),
        ];
        if (payment) {
          payment.isRemoved = ee.isRemoved;
          changes.push(cif.update('EmployeePaymentMethod', payment));
        }
        return this.saveEntities({ changes }, environment.apiRoot + '/save/employee/removed').pipe(
          concatMap(() => this.taxCollectorService.computeTaxes().pipe(map(() => ee)))
        );
      }),
    );
    return result$;
  }
}

