import { Injectable } from '@angular/core';
import { CanActivateChild } from '@angular/router';

import { Observable } from 'rxjs';
import { CompanyDeductionEditorService } from './company-deduction-editor.service';

@Injectable({ providedIn: 'root' })
export class CompanyDeductionsGuard implements CanActivateChild {

  constructor(private service: CompanyDeductionEditorService) { }

  canActivateChild(): Observable<boolean> {
    return this.service.ready();
  }
}
